.back2Top {
    width: 67px;
	height: 67px;
    overflow: hidden;
    z-index: 999;
    cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
    position: fixed;
    bottom: 100px;
    right: -35px;
    background-color: transparent;
    text-decoration: none;
	border: 1px solid #216AB1;
	border-radius: 50%;
	opacity: 0;
	visibility: hidden;
	transition: all .3s ease-in-out;
}

.back2Top.show {
	opacity: 1;
	visibility: visible;
	right: 35px;
}
