.pagination {
  padding: 12px 0;
}

.pagination-link {
  text-decoration: underline !important;
}

.pagination-list__button {
  border: none;
  background: transparent;
}

.pagination__list-item {
  padding: 0 6px;
}

.pagination__link {
  color: var(--text_primary);
  font-weight: bold;
  &_active {
    color: var(--text_secondary);
  }
}

@media screen and (min-width: 768px) {
  .pagination__list-item {
    padding: 0 12px;
  }
}
