@media screen and (min-width: 1025px) {
  .sh {
    padding: 24px 0;
    display: flex;
  }
  .sh_left {
    width: 58%;
  }
  .sh_right{
    width: 42%;
  }
  .sh__logo-link {
    width: 150px;
    margin: 0 10px 0 0;
    .sh__logo-icon{
      width: 150px;
    }
  }

  .sh__catalog-button {
    max-width: 150px;
    width: 100%;
    background: var(--link-color);
    color: #ffffff;
    border: none;
    height: 54px;
  }
  .sh__search-form {
    padding: 0 0 0 32px;
  }
  .sh__search-input {
    border: none;
    border-bottom: 1px solid var(--border-color);
    margin: 0 12px 0 0;
  }
  .sh__search-button {
    border: none;
    background: transparent;
  }
  .sh__list {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
  }
  .sh__list-item {
    padding: 0 10px;
    border-right: 1px solid var(--border-color);
    &:last-child {
      border: none;
      align-self: center;
    }
  }
  .sh__list-button {
    border: none;
    background: transparent;
    position: relative;
  }
  .sh__list-button-label {
    padding: 4px 12px;
    font-size: 12px;
    border-radius: 24px;
    position: absolute;
    color: var(--text_contrast);
    background: var(--link-color);
    right: 0;
    top: -8px;
  }
  .sh__list-button-icon {
    margin: 0 0 8px 0;
  }
  .sh_cart-button {
    border: none;
    background: none;
  }
  .sh_cart-label {
    background: var(--primary);
    padding: 4px 8px;
    color: var(--text_contrast);
    border-radius: 24px;
    margin: 0 8px;
  }
}

@media screen and (min-width: 1366px) {
  .sh {
    flex-flow: row nowrap;
    padding: 0 12px;
  }
  .sh_left {
    width: 60%;
  }
  .sh__logo-link {
    width: 150px;
  }
  .sh__catalog-button {
    width: 200px;
    margin: 0 24px 0 48px;
  }
  .sh_right {
    width: 40%;
    justify-content: flex-end;
  }
  .sh__list {
    transform: scale(.8);
  }
}

@media screen and (min-width: 1440px) {
  .sh__list {
    transform: scale(1);
  }
  .sh__list-item {
    &:last-child {
      padding: 0 0 0 24px;
    }
  }
}
