.footer__list-item {
  display: none;
}

.footer__list-item_header {
  display: block;
  margin: 0 0 7px 0;
}

.footer__contacts-wrapper {
  padding: 24px 0;
  border-top: 1px solid var(--border-color);
  margin: 24px 0 0 0;
}

.footer__contact-item-icon {
  margin: 0 8px 0 0;
}

.footer__contact-item {
  &:last-child {
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;
  }
}

.footer__social-wrapper {
  padding: 12px 0 24px 0;
}

.footer__copy {
  background: #F0F0F0;
  padding: 12px;
  flex-flow: column nowrap;
  align-items: center;
  * {
    color: #5F5F5F;
    font-size: 12px;
  }
}

.footer__logo {
  display: none;
}

.footer__social-list-icon {
  width: 24px;
  height: 24px;
}

@media screen and (min-width: 768px) {
  .footer {
    justify-content: space-between;
  }
  .footer__contacts-wrapper {
    padding: 0;
    margin: 0;
    width: 50%;
    border: none;
  }
}

@media screen and (min-width: 1025px) {
  .footer__lists-wrapper {
    display: flex;
    flex-flow: row nowrap;
  }
  .footer__list {
    &:first-child {
      width: 180px;
    }
    &:last-child {
      width: 300px;
    }
  }
  .footer__list-item {
    display: block;
    color: var(--text_secondary);
    margin: 0 0 4px 0;
  }
  .footer__copy {
    flex-flow: row nowrap;
    justify-content: space-between;
  }
}

@media screen and (min-width: 1200px) {
  .footer__lists-wrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 100%;
  }
  .footer__list-item {
    margin: 0 0 12px 0;
  }
  .footer__list {
    &:first-child {
      width: 150px;
    }
    &:last-child {
      width: 350px;
      padding: 0 0 0 32px;
    }
  }
}

@media screen and (min-width: 1366px) {
  footer {
    border-top: 1px solid var(--border-color);
    padding: 32px 0 0 0;
  }
  .footer {
    flex-flow: row nowrap;
    width: 100%;
    justify-content: space-between;
    align-items: flex-start;
  }
  .footer__social-wrapper {
    order: 1;
    width: 210px;
    align-items: flex-start;
  }
  .footer__lists-wrapper {
    order: 2;
    width: calc(100% - 600px);
  }
  .footer__contacts-wrapper {
    order: 3;
    width: 300px;
  }
  .footer__logo {
    display: block;
  }
}
