.advertising-banner {
  flex-flow: column nowrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  padding: 32px 24px;
}

.advertising-banner__image {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: auto;
  height: 100%;
}

.advertising-banner__title {
  position: relative;
  z-index: 100;
  color: var(--text_contrast);
  text-align: center;
  line-height: 100%;
  text-transform: uppercase;
  font-weight: 700;
}

.advertising-banner__button {
  width: 100%;
  background: var(--block-background);
  position: relative;
  z-index: 100;
  border-radius: 50px;
  margin: 64px 0 0 0;
}

@media screen and (min-width: 1025px) {
  .advertising-banner {
    flex-flow: row nowrap;
  }
  .advertising-banner__image {
    width: 100%;
    height: auto;
  }
  .advertising-banner__title {
    max-width: 40%;
    text-align: left;
  }
  .advertising-banner__button {
    margin: 0;
    width: auto;
    min-width: 200px;
    font-weight: bold;
  }
}

.advertising-banner2{
  background-image: url('../img/marketing-offer.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  padding: 75px;
}
.advertising-banner2-grid{
  grid-area: banner;
}

@media screen and (max-width: 576px) {
  .advertising-banner2{
    padding: 75px 20px;
  }
}