.coocie{
	position: fixed;
    bottom: 0;
    z-index: 800;
    width: 100%;
    background-color: #fff;
	box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
	transition: all ease .2s;
}
.coocie__wrap{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 19px 0;
	.button{
		text-transform: uppercase;
		border: 1px solid #000;
		width: 60px;
		flex-shrink: 0;
	}
}
.coocie.active{
	opacity: 0;
    visibility: hidden;
    transition: all ease .2s;
}