.offer-card {
  width: 100%;
  height: auto;
  border: 1px solid var(--border-color);
  position: relative;
  overflow: hidden;
}

.offer-card__due-label {
  position: absolute;
  left: 14px;
  top: 13px;
  padding: 4px 15px;
  background: var(--due-label);
  color: var(--text_contrast);
  font-size: 12px;
  font-weight: 500;
  z-index: 300;
  display: flex;
  align-items: center;
}

.offer-card__due-icon {
  margin: 0 8px 0 0;
}

.offer-card__image-wrapper {
  height: 295px;
  overflow: hidden;
}

.offer-card__image {
  height: 100%;
  width: auto;
  transition: all .3s ease-in-out;
}

.offer-card__info-wrapper {
  padding: 24px 10px 16px 18px;
  width: 100%;
}

.offer-card__title {
  font-size: 20px;
  color: var(--text_primary);
  margin: 0 0 16px 0;
}

.offer-card__description {
  line-height: 24px;
}

@media screen and (min-width: 768px) {
  .offer-card {
    max-width: 490px;
  }
}

@media screen and (min-width: 1025px) {
  .offer-card {
    transition: all .125s ease-in-out;
    &:hover {
      .offer-card__image {
        transform: scale(1.05);
        z-index: 1;
      }
    }
  }
}
