.article-card {
  flex-flow: column nowrap;
  border: 1px solid var(--border-color);
}

.article-card__image-wrapper {
  height: 204px;
  overflow: hidden;
}

.article-card__image {
  width: 100%;
  // height: auto;
  height: 100%;
  transition: all .3s ease-in-out;
}

.article-card__info-wrapper {
  padding: 14px 13px 21px 20px;
}

.article-card__title {
  color: var(--text_primary);
  margin: 0 0 8px 0;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}

.article-card__description {
  line-height: 24px;
  font-size: 16px;
}

.article-card__date {
  margin: 12px 0 0 0;
  display: flex;
  align-items: center;
}

.date-calendar {
  margin: 0 8px 0 0;
  transform: translateY(-1px);
}

@media screen and (min-width: 768px) {
  .article-card {
    flex-flow: row nowrap;
    transition: all .125s ease-in-out;
    &_column {
      flex-flow: column nowrap;
      max-width: 358px;
    }
    &:hover {
      box-shadow: 0 0 16px rgba(0, 0, 0, .25);

      .article-card__image {
        transform: scale(1.05);
      }
    }
  }



  .article-card__title {
    &:hover {
      color: var(--link-color);
      text-decoration: underline;
    }
  }
}

@media screen and (min-width: 1025px) {
  .article-card__image-wrapper {
    width: 302px;
    height: 100%;
  }
  .article-card__info-wrapper {
    width: calc(100% - 302px);
  }
  .article-card_column {
    .article-card__image-wrapper {
      width: 100%;
    }
    .article-card__info-wrapper {
      width: 100%;
    }
  }
}
