@import "helpers/variables";
@import "../blocks/modules";

.banners__slider {
  width: 100%;
  height: 300px;
  overflow: hidden;
  position: relative;
}

.banners-slider__button {
  position: absolute;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  top: calc(50% - 25px);
  background: var(--block-background);
  z-index: 100;
  * {
    pointer-events: none;
  }

  &_prev {
    left: -25px;
    padding: 0 0 0 16px;
  }

  &_next {
    right: -25px;
    padding: 0 16px 0 0;
  }
}

.banners-row {
  width: auto;
  height: 100%;
}

.banner__wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.banner__image {
  width: auto;
  height: 300px;
}

.static-banners__wrapper {
  width: 100%;
}

.static-banner__wrapper {
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
  margin: 24px 0 0 0;
}

.static-banner__img {
  width: 100%;
  height: auto;
}

.banner__link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 300;
}

.custom-offer__wrapper {
  width: 100%;
  height: 135px;
  background: var(--block-background);
  padding: 20px 40px 20px 20px;
  position: relative;
}

.custom-offer__image-wrapper {
  width: 40px;
  height: 40px;
}

.custom-offer__image {
  width: 100%;
}

.custom-offer__info-wrapper {
  width: calc(100% - 60px);
  padding: 0 12px;
}

.custom-offer__description {
  margin: 8px 0 0 0;
}

.custom-offer__arrow-wrapper {
  position: absolute;
  right: 0;
  width: 20px;
  height: 20px;
}

.offers__wrapper {
  width: 100%;
}

.offer-card {
  margin: 0 0 32px 0;
}

.popular-categories__wrapper {
  display: block;
}

.popular-category__wrapper {
  width: 100%;
  height: 200px;
  position: relative;
  padding: 36px;
  margin: 0 0 16px 0;
}

.popular-category__link {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 200;
  width: 100%;
  height: 100%;
}

.popular-category__filter {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}

.popular-category__title {
  z-index: 100;
  width: 60%;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
}

.popular-category__image {
  position: absolute;
  right: 12px;
  bottom: 12px;
  width: 60%;
  height: auto;
  z-index: 100;
}

.best-products__tabs-header {
  flex-flow: row nowrap;
  overflow-x: auto;
}

.best-products__tabs-button {
  text-transform: uppercase;
  margin: 0 24px 0 0;
  border-bottom: 4px solid transparent;
  z-index: 100;

  &_popular {
    color: var(--link-color);
    &.active {
      border-color: var(--link-color);
      font-weight: bold;
    }
  }
  &_sale {
    color: var(--alarm);
    &.active {
      border-color: var(--alarm);
      font-weight: bold;
    }
  }
  &_new {
    color: var(--success);
    &.active {
      border-color: var(--success);
      font-weight: bold;
    }
  }
  &_all {
    color: var(--link-color);
    text-decoration: underline;
    position: relative;
    &::before {
      width: 1px;
      height: 20px;
      content: '';
      display: block;
      background: var(--border-color);
      position: absolute;
      left: -12px;
    }
  }
}

.tab-slider__wrapper {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  .swiper-container {
    width: 100%;
  }
  &.hidden {
    display: none;
  }
}

.products-slider__controls-wrapper {
  width: 100%;
}

.section-title__video {
  display: flex;
  align-items: center;
}

.video-title__icon-wrapper {
  width: 41px;
  height: 41px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 6px solid #333333;
  margin: 0 21px 0 0;
}

.video-title__icon-wrapper-my{
  object{
    position: relative;
    right: -1px;
  }
}

.video-reviews-section {
  background: #F5F5F5;
}

.video-review__wrapper {
  width: 100%;
  height: 300px;
}

.video-review-card {
  position: relative;
  padding: 24px 0 0 0;
}

.video-review-card__link {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.video-review-card__image-wrapper {
  position: relative;
  width: 100%;
  height: 221px;
  overflow: hidden;
}

.video-review-card__play-button {
  position: absolute;
  width: 80px;
  height: 80px;
  border: 6px solid rgba(255, 255, 255, .5);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-review-card__title {
  margin: 12px 0;
}

.video-review-card__description {
  line-height: 24px;
}

.video-review-card__image {
  height: 100%;
  // height: auto;
  width: 100%;
}

.all-reviews-button {
  margin: 24px 0 0 0;
  font-weight: bold;
  border-color: #000000;
}

.articles-section__header-link {
  margin: 0 0 24px 0;
  text-transform: uppercase;
  text-decoration: underline;
}

.article-card {
  margin: 0 0 32px 0;
}

.brands-slider {
  height: 84px;
}

.brand__wrapper {
  padding: 12px;
  border: 1px solid var(--border-color);
  display: flex;
  justify-content: center;
}

.brand__image {
  width: auto;
  height: 100%;
}

.brands__controls {
  display: flex;
}

.product-card {
  border: none;
}

.tab-slider__wrapper {
  &.hidden {
    display: none;
  }
}
.tab-slider__wrapper {
  height: calc(100% - 100px);
}
.product-slider__controls {
  display: flex;
  flex-flow: row nowrap;
}

.best-products__tabs-button_sale {
  span {
    margin: 0 4px 0 0;
  }
}

@media screen and (min-width: 768px) {
  .banners-row {
    width: 100%;
  }
  .banner__wrapper {
    width: 100%;
  }
  .banner__image {
    width: 100%;
    height: auto;
  }
  .static-banners__wrapper {
    justify-content: space-between;
    flex-wrap: nowrap;
  }
  .static-banner__wrapper {
    width: calc(50% - 12px);
  }
  .static-banner__image {
    height: 100%;
    width: auto;
  }
  .custom-offers__wrapper {
    flex-wrap: nowrap;
  }
  .custom-offer__wrapper {
    width: calc(50% - 12px);
  }
  .offer-card {
    width: calc(33% - 6px);
  }
  .popular-categories__wrapper {
    display: grid;
    grid-template-areas: 'a a';
    gap: 24px;
  }
  .popular-category__wrapper {
    margin: 0;
  }
  .best-products__header {
    flex-flow: column;
    align-items: flex-start;
  }
  .best-products__tabs-header {
    width: 100%;
  }
  .articles__wrapper {
    flex-wrap: nowrap;
  }
  .article-card {
    flex-flow: column nowrap;
    width: calc(50% - 12px);
  }
  .video-reviews-cards__wrapper {
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  .video-review-card {
    width: calc(50% - 12px);
  }
  .brands-slider {
    flex-wrap: nowrap;
  }
  .brand__wrapper {
    width: 33%;
  }
}

@media screen and (max-width: 1200px){
  .article-card{
    flex-flow: column nowrap;
    width: calc(50% - 12px);
  }
  .article-card__image-wrapper{
    width: 100%;
  }
  .article-card__info-wrapper{
    width: 100%;
  }
}
@media screen and (max-width: 576px){
  .articles__wrapper{
    flex-direction: column;
  }
  .article-card{
    width: 100%;
  }
}

@media screen and (min-width: 1025px) {
  .banners__wrapper {
    justify-content: space-between;
  }
  .banners__slider {
    width: calc(100% - 430px);
    height: 540px;
  }
  .banner__image {
    height: 100%;
    width: auto;
  }
  .static-banners__wrapper {
    flex-flow: column nowrap;
    width: 390px;
    justify-content: space-between;
  }
  .static-banner__wrapper {
    width: 100%;
    height: 245px;
    margin: 0;
  }
  .custom-offers__wrapper {
    padding: 0 0 59px 0;
  }
  .custom-offer__wrapper {
    transition: all .125s ease-in-out;
    border-radius: 5px;
    &:hover {
      box-shadow: 0 0 16px rgba(0, 0, 0, .25);
      cursor: pointer;
    }
  }
  .custom-offer__image-wrapper {
    width: 68px;
    height: 68px;
  }
  .custom-offer__info-wrapper {
    width: calc(100% - 80px);
  }
  .custom-offer__arrow-wrapper {
    right: 52px;
  }
  .popular-categories__wrapper {
    grid-template-areas: 'a a a a';
    grid-auto-columns: 1fr 1fr 1fr 1fr;
  }
  .popular-category__image {
    transition: all .3s ease-in-out;
  }
  .popular-category__wrapper {
    height: 235px;
    overflow: hidden;
    &:hover {
      cursor: pointer;
      overflow: hidden;
      .popular-category__image {
        transform: scale(1.05);
      }
    }
  }
  .best-products__header {
    flex-flow: row nowrap;
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background: var(--border-color);
      position: absolute;
      left: 0;
      bottom: 22px;
      z-index: 1;
    }
  }
  .article-card {
    flex-flow: row nowrap;
  }
  .video-reviews__wrapper {
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  .video-review__wrapper {
    width: calc(50% - 20px);
    // height: 430px;
    height: auto;
  }
  .video-reviews-control__wrapper {
    width: calc(50% - 20px);
  }
  .video-review-card {
    padding: 0;
  }
  .best-products__tabs-header {
    width: 45%;
  }
  .product-card {
    width: 25%;
  }
  .tab-slider__wrapper {
    position: relative;
    height: 100%;
    .swiper-wrapper {
      height: 90%;
    }
  }
  .product-slider__controls {
    position: absolute;
    width: 100%;
    top: 50%;
    display: flex;
    justify-content: space-between;
    z-index: 500;
  }
  .products-slider__control-button {
    width: 50px;
    height: 50px;
    &.prev {
      transform: translateX(-32px);
    }
    &.next {
      transform: translateX(32px);
    }
  }
  .brands__wrapper {
    position: relative;
  }
  .brands__controls {
    position: absolute;
    width: 100%;
    top: 20%;
    display: flex;
    justify-content: space-between;
    z-index: 500;
  }
  .brands-controls__button {
    width: 50px;
    height: 50px;
    &_prev {
      transform: translateX(-40px);
    }
    &_next {
      transform: translateX(40px);
    }
  }
}

@media screen and (min-width: 1366px) {
  .banners__slider {
    width: calc(100% - 520px);
  }
  .static-banners__wrapper {
    width: 490px;
  }
  .container {
    max-width: 1600px;
  }
  .product-card {
    width: 20%;
  }
}

@media screen and (max-width: 1280px) {
  .best-products__header{
    flex-direction: column;
    &::after{
      bottom: 0;
    }
  }
  .best-products__tabs-header{
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .best-products__tabs-header{
    flex-direction: column;
    align-self: flex-start;
    width: auto;
  }
  .best-products__header{
    text-align: left;
  }
  h3{
    width: 100%;
  }
  .best-products__tabs-button{
    margin: 0;
  }
}

.coocie.active{
  opacity: 0;
  visibility: hidden;
  transition: all ease .2s;
}

