// Navigation
.header-first__wrapper {
  width: 100%;
  height: 29px;
  background: #F0F0F0;
}
.container_header {
  // padding: 0;
}
.header-first__left-nav {
  display: none;
}
.header-first__right-nav {
  flex-flow: row-reverse nowrap;
  justify-content: center;
  width: 100%;
}
.header-first__right-nav-title_mobile-hidden {
  display: none;
}

.header-first__right-nav-link {
  font-weight: bold;
  margin: 0 12px 0 0;
  color: var(--text_primary);
}

.header-second__wrapper {
  height: 56px;
  align-items: center;
  position: sticky;
  top: 0;
  padding: 0 12px;
}

.header_default {
  position: sticky;
  top: -29px;
  z-index: 600;
  background: var(--block-background);
}

.header_scrolled {
  position: sticky;
  top: 0;
  z-index: 900;
  background: #ffffff;
  transform: translateY(0);
  box-shadow: 0 0 18px rgba(0, 0, 0, .25);
  opacity: 1;
  transition: all .3s;
  padding: 15px 0;
  &.hidden-top {
    transform: translateY(-250px);
    opacity: 0;
    z-index: -1;
    position: absolute;
  }
}

.header-second__menu-button {
  border: none;
  background: transparent;
  outline: none;
  margin: 0 15px 0 0;
  transition: all .125s;
  * {
    pointer-events: none;
  }
  &.active {
    transform: rotate(90deg);
  }
}
.mobile-menu__list {
  position: absolute;
  left: 12px;
  width: calc(100vw - 24px);
  max-width: 400px;
  background: #ffffff;
  padding: 16px;
  top: 70px;
  box-shadow: 0 0 30px rgba(0, 0, 0, .25);
  z-index: 400;
  display: none;
  &.active {
    display: flex;
    flex-flow: column nowrap;
  }
}
.mobile-menu__list-item {
  margin: 0 0 16px 0;
}
.header-third__wrapper {
  position: relative;
}
.mobile-header-nav {
  position: fixed;
  top: 140px;
  width: calc(100vw - 24px);
  max-width: 400px;
  max-height: 70vh;
  overflow-y: auto;
  left: 12px;
  background: #ffffff;
  padding: 12px;
  z-index: 400;
  box-shadow: 0 0 30px rgba(0, 0, 0, .25);
  display: none;
  &.active {
    display: flex;
    flex-flow: column nowrap;
  }
}
.mobile-header-nav__item {
  padding: 12px 0;
}
.mobile-header-nav__link {
  display: flex;
  align-items: center;
  &.nested-contains {
    &.expanded {
      font-weight: bold;
      &::before {
        content: url('/img/svg/arrow_left-mobile-menu.svg');
        display: block;
        margin: 0 17px 0 0;
      }
    }
    &::after {
      content: url('/img/svg/arrow_right-mobile-menu.svg');
      display: block;
      margin: 0 0 0 17px;
    }
    &::before {
      display: none;
    }
  }

}
.mobile-header-nav__nested-list {
  display: none;
  &.active {
    display: flex;
    flex-flow: column nowrap;
  }
}
.mobile-header-nav__nested-list-item {
  padding: 12px 0;
}
.header-second__logo-link {
  width: 120px;
}
.header-second__logo-image {
  width: 100%;
  height: auto;
}

.header-second__right-nav-button {
  width: 32px;
  height: 32px;
  border: none;
  background: transparent;
  margin: 0 12px;
  position: relative;
}

.header-second__right-nav-button-label {
  position: absolute;
  right: -16px;
  top: -8px;
  background: var(--primary);
  color: #ffffff;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.header-third__catalog-trigger-button {
  border-radius: 0;
  border: none;
  color: #ffffff;
  background: var(--link-color);
  text-transform: uppercase;
  justify-content: flex-start;
  padding: 0 10px;
  position: relative;
}

.header-second__right-nav-button-title {
  display: none;
}
.hidden_mobile {
  display: none;
}

.trigger__burger {
  width: 18px;
  height: 18px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  margin: 0 30px 0 0;
  transition: all .3s;
}

.trigger__arrow {
  position: absolute;
  right: 12px;
  transition: all .3s;
}

@media screen and (min-width: 768px) {
  .container_header {
    max-width: 100vw;
  }
}

@media screen and (min-width: 1025px) {
  .container_header {
    max-width: 1366px;
  }
