  }
  .catalog-drop__column {
    width: 25%;
    padding: 0 12px;
  }
  .catalog-drop__category-wrapper {
    padding: 0 0 32px 0;
  }
  .catalog-drop__category-image-wrapper {
    min-width: 58px;
    min-height: 58px;
    max-width: 58px;
    max-height: 58px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .catalog-drop__category-image {
    width: 100%;
    height: auto;
  }
  .catalog-drop__list {
    padding: 0 0 0 16px;
  }
  .catalog-drop__list-link {
    margin: 0 0 4px 0;
    display: block;
    &:hover {
      color: var(--primary);
    }
  }
  .catalog-drop__list-link_expand {
    display: flex;
    align-items: center;
  }
  .catalog-drop__expand-value {
    text-decoration: underline;
  }
  .catalog-drop__expand-icon {
    margin: 0 0 0 8px;
  }
  .catalog-drop__list-link_header {
    font-weight: bold;
    font-size: 18px;
    margin: 0 0 8px 0;
    display: block;
  }
  .header_default {
    position: relative;
    top: 0;
  }
  .header-first__wrapper {
    width: 100%;
  }
  .header-first__left-nav {
    display: flex;
    flex-flow: row nowrap;
    width: 60%;
    padding: 0 12px;
  }
  .header-first__left-nav-item {
    margin: 0 24px 0 0;
    &:last-child {
      margin: 0;
    }
  }
  .header-first__right-nav {
    flex-flow: row nowrap;
    justify-content: flex-end;
    width: 40%;
  }
  .header-first__right-nav-title_mobile-hidden {
    display: block;
    float: left;
  }
  .header-first__right-nav-link {
    display: block;
    margin: 0 0 0 8px;
  }
  .header-second__wrapper {
    height: 98px;
  }
  .header-second__menu-button {
    display: none;
  }
  .header-second__logo-link {
    width: 225px;
  }
  .header-second__right-nav-button {
    flex-flow: column nowrap;
    align-items: center;
    display: flex;
    margin: 0 72px 0 0;
    border-right: 1px solid var(--border-color);
    width: auto;
    padding: 0 24px 0 0;
    &:nth-of-type(4) {
      margin: 0;
      border: none;
      padding: 0;
    }
  }
  .header-second__right-nav-button-title {
    display: block;
    margin: 4px 0 0 0;
  }
  .hidden_desktop {
    display: none;
  }
  .header-second__search-form {
    display: block;
    padding: 0 24px 0 0;
    width: 311px;
  }
  .input_search {
    border: none;
    outline: none;
    border-bottom: 1px solid var(--border-color);
    background: transparent;
  }
  .search-button {
    border: none;
    background: transparent;
  }
  .header-third__wrapper {
    align-items: center;
    position: relative;
  }
  .header-third__nav {
    display: flex;
    flex-flow: row nowrap;
  }
  .header-third__catalog-trigger-button {
    width: 361px;
    &:hover {
      .trigger__burger {
        transform: rotate(90deg);
      }
      .trigger__arrow {
        transform: rotate(180deg);
      }
    }
  }
  .header-third__nav-list-link {
    display: block;
    width: auto;
    text-align: center;
    font-weight: bold;
  }
  .header-third__cart-button {
    display: flex;
    align-items: center;
    border: none;
    background: transparent;
    float: right;
    position: absolute;
    right: 0;
  }
  .header-third__cart-label {
    margin: 0 4px;
    color: #ffffff;
    background: var(--primary);
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 12px;
  }
}

