  flex: 0 1 80%;
  justify-content: flex-end;
}
.header-second__search-form.active{
  width: 71%;
  padding: 0;
  margin-right: 20px;
}
.header-third__wrapper.active{
  z-index: -1;
}
.header-third__wrapper-t.active{
  z-index: -1;
}
.search-form__label.active{
  border: 1px solid #D0D0D0;
  .input_search{
    border: none;
  }
  .search-button{
    padding-right: 16px;
  }
}
.sh-rel{
  position: relative;
}
.block{
  display: none;
}
.block-sm.active{
  top: 0;
  left: 0;
  height: 400px;
  overflow-y: scroll;
}
.block-sh.active{
  top: 0;
  left: 0;
  height: 400px;
  overflow-y: scroll;
}
.block.active{
  display: block;
  position: absolute;
  z-index: 100;
  width: 100%;
  // max-height: 680px;
  background: #FFFFFF;
  box-shadow: 0px 0px 22px rgba(0, 0, 0, 0.25);
  .block__category{
    background: #F4F4F4;
    padding: 21px 36px;
    h3{
      font-size: 18px;
      text-transform: uppercase;
      margin-bottom: 25px;
    }
    li{
      margin-bottom: 15px;
      padding-bottom: 15px;
      border-bottom: 1px solid #D5D5D5;
      &:last-child{
        border-bottom: none;
        margin: 0;
        padding: 0;
      }
    }
  }
  .block__items{
    padding: 21px 36px;
    background-color: #fff;
    h3{
      font-size: 18px;
      text-transform: uppercase;
      margin-bottom: 25px;
    }
    a{
      color: #005CB6;
    }
  }
  .block__item{
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #D5D5D5;
    display: flex;
  }
  .block__img{
    flex-shrink: 0;
    margin-right: 40px;
  }
  .block__body{
    p{
      font-size: 18px;
      line-height: 20px;
      color: #005CB6;
    }
  }
}


@media screen and (min-width: 1440px) {
