.product-card {
  position: relative;
  width: 100%;
  padding: 0px 20px 10px 20px;
  background: var(--block-background);
  border-top: 1px solid var(--border-color);
}

.product-card__image-wrapper {
  width: 100%;
  height: 242px;
  overflow: hidden;
  position: relative;
}

.product-card__image {
  width: 100%;
  height: 100%;
  transition: all .3s ease-in-out;
}

.product-card__product-link {
  margin: 14px 0 18px 0;
  &:hover {
    text-decoration: underline;
  }
}

.product-card__buttons-wrapper {
  position: relative;
  .button {
    color: #ffffff;

    &.added {
      border-color: transparent;
      background: #E9E9E9;
      color: var(--text_primary);
    }
  }
}

.button_to-basket-null {
  color: var(--text_primary) !important;
}

.product-card__feature-wrapper {
  padding: 0 0 8px 0;
  * {
    &:first-child {
      margin: 0 4px 0 0;

    }
  }
}

.product-card__price-wrapper {
  padding: 22px 0 15px 0;
}

.product-card__old-price {
  margin: 0 0 0 8px;
}

.button_compare {
  width: 53px;
}

.button_favorite {
  width: 53px;
}

.button_to-basket {
  width: calc(100% - 120px);
}

.product-card__footer-wrapper {
  width: 100%;
  position: absolute;
  height: 68px;
  bottom: -80px;
  left: 0;
  opacity: 0;
  z-index: -1;
  pointer-events: none;
  transition: all .125s ease-in-out;
  transition-delay: .1s;
  background: var(--block-background);

  &::before {
    content: '';
    display: block;
    background: var(--block-background);
    width: 100%;
    height: 40px;
    position: absolute;
    z-index: 300;
    top: -20px;
  }
  &::after {
    content: '';
    display: block;
    width: calc(100% - 32px);
    height: 1px;
    background: var(--border-color);
    position: absolute;
    top: 0;
    left: 16px;
    z-index: 400;
  }
}

.product-card__one-click-button {
  color: var(--link-color);
}

.product-card__labels-wrapper {
  position: relative;
  left: 0;
  top: 22px;
  width: 100%;
  flex-flow: row nowrap;
  padding: 0px 12px 5px 12px;
  z-index: 100;
}

.new-label {
  width: 30px;
}

.offer-label {
  background: var(--alarm);
  color: #ffffff;
  padding: 4px 8px;
  font-size: 13px;
}

.hit-label {
  background: var(--link-color);
  color: #ffffff;
  padding: 4px 8px;
  font-size: 13px;
  margin: 0 0 0 8px;
}



.cart-tooltip {
  background: #ffffff;
  width: 308px;
  padding: 19px 23px;
  box-shadow: 0 0 22px rgba(0, 0, 0, .25);
  position: absolute;
  top: -160px;
  right: 0;
  &::after {
    content: '';
    display: block;
    position: absolute;
    right: 12px;
    bottom: -12px;
    background: white;
    transform: rotate(45deg);
    width: 24px;
    height: 24px;
  }
}

.cart-tooltip__icon {
  margin: 0 8px 0 0;
  width: 24px;
  height: 24px;
}

.cart-tooltip__title {
  font-size: 16px;
  font-weight: 500;
  color: var(--primary);
}

.cart-tooltip__product-title {
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  margin: 12px 0;
}

.cart-tooltip__link {
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .product-card {
    max-width: 360px;
  }
}

@media screen and (min-width: 1025px) {
  .product-card {
    transition: all .125s ease-in-out;
    &::before {
      width: 100%;
      height: 4px;
      display: block;
      content: '';
      background: var(--primary);
      position: absolute;
      top: -4px;
      left: 0;
      opacity: 0;
      transition: all .125s ease-in-out;
    }
    &:hover {
      box-shadow: 0 0 19px rgba(0, 0, 0, .25);
      &::before {
        top: 0;
        opacity: 1;
      }
      .product-card__image {
        transform: scale(1.05);
      }
      .product-card__footer-wrapper {
        opacity: 1;
        // bottom: -68px;
        z-index: 300;
        pointer-events: auto;
        box-shadow: 0 0 19px rgba(0, 0, 0, .25);
      }
    }
  }
}

